body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  margin-bottom: 10vw;
}
.uploadImage {
  margin: 0 auto;
  position: relative;
  width: 700px;
  height: 400px;
  border: 3px dashed gray;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 5vw; }
  .uploadImage .input {
    opacity: 0;
    background-color: bisque;
    width: 700px;
    height: 400px;
    position: absolute;
    top: 0;
    left: 0;
    cursor: pointer; }

.imageInfo {
  display: flex;
  justify-content: space-around;
  margin-top: 5vw; }
  .imageInfo img {
    width: 32vw;
    height: 18vw; }
  .imageInfo .imageInfo__number {
    width: 40vw; }

.cssInfoBox .cssInfoBox__input {
  display: flex;
  justify-content: space-between; }
  .cssInfoBox .cssInfoBox__input input {
    width: 18vw;
    height: 5vw; }

.cssInfoBox .cssInfoBox__info {
  display: flex;
  flex-direction: column; }
  .cssInfoBox .cssInfoBox__info .cssInfoBox__info_main {
    margin-bottom: 5vw; }
  .cssInfoBox .cssInfoBox__info .cssInfoBox__title {
    font-weight: 600;
    font-size: 20px; }
  .cssInfoBox .cssInfoBox__info .cssInfoBox__max {
    margin-top: 2vw; }
  .cssInfoBox .cssInfoBox__info .cssInfoBox__web {
    margin-top: 5vw; }
  .cssInfoBox .cssInfoBox__info .cssInfoBox__mobile {
    margin-top: 5vw; }

