.cssInfoBox{
  
  .cssInfoBox__input{
    display: flex;
    justify-content: space-between;
    input{
      width: 18vw;
      height: 5vw;
    }
  }
  .cssInfoBox__info{
    display: flex;
    flex-direction: column;
    .cssInfoBox__info_main{
      margin-bottom: 5vw;
    }
    .cssInfoBox__title{
      font-weight: 600;
      font-size: 20px;
    }
    .cssInfoBox__max{
      margin-top: 2vw;
    }
    .cssInfoBox__web{
      margin-top: 5vw;
    }
    .cssInfoBox__mobile{
      margin-top: 5vw;
    }
  }
  
}